body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* body {
  background-color: rgb(2 18 40)
} */

.App {
  margin: 90px auto;
  max-width: 90%;
}

.App #mainLogo h1 {
  margin-bottom: 0;
}

.navigation {
  position: fixed;
  top: 0px;
  background: white;
  width: 90%;
}

.nav-bar {
  height: 20px;
  padding-top: 2px;
  background-color: lightblue;
}

.nav-bar a {
  padding-top: 20px;
  margin-right: 20px;
  color: black;
  text-decoration: none;
}

.nav-bar a:first-of-type {
  margin-left: 10px;
}

.welcome {
  width: 91%;
  height: auto;
  padding: 50px;
  border: 1px solid black;
}

.signup-form, .login-form {
  background-color: white;
  margin: 10px 0;
}

.welcome-bg {
  width: 520px;
  height: 520px;
  background: url('https://thetabletales.com/wp-content/uploads/2018/01/Chefs-Table-Omakase-Menu-1000x400.jpg') no-repeat;
}

.recipe-card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  margin: 20px 10px;
  max-width: 250px;
  height: 400px;
  overflow: hidden;
  float: left;
  background-color: white;
}

.recipe-card-image {
  width: 250px;
  height: 250px;
}

.recipe-card-image:hover {
  width: 250px;
  height: 250px;
}

.recipe-card a {
  color: black;
  text-decoration: none;
}

/* On mouse-over, add a deeper shadow */
.recipe-card:hover {
  box-shadow: 0 8px 16px 0 black;
}

/* Add some padding inside the card container */
.container {
  padding: 5px 5px;
}

.recipe-header {
  display: inline;
}

.title, .cals-per-serving {
  display: block;
  font-family: 'Bitter', serif;
}

.cals-per-serving {
  font-size: 20px;
  padding: 180px 10px 0 10px;
  opacity: 0;
  transition: 1s;
}

.cals-per-serving:hover {
  opacity: 0.7;
  background-color: white;
  height: 100%;
  transition: 1s;
}

h1 {
  font-family: 'Bitter', serif;
}

.full-recipe-card {
  width: 55%;
  float: left;
}

.full-recipe-card img {
  width: 100%;
}

.ingredients-table {
  float: left;
  margin-left: 20px;
  width: 40%;
}

.ingredients-table h1, .full-recipe-card h1 {
  margin-bottom: 0;
}

.fake-link, .ingredients-table a {
  text-decoration: none;
  font-size: 12px;
  color: black;
  cursor: pointer;
}

.full-recipe-card form {
  margin-top: 10px;
}

.ingredients-table table {
  margin-top: 10px;
}

.preview-image {
  height: 300px;
  width: auto;
}

.search {
  margin: 10px 0 -10px 10px;
}

.search form input {
  outline: none;
  width: 245px;
  height: 25px;
}

table {
  outline: none !important;
  font-size: 10px;
}

tr:nth-child(2n+0) {
  background-color: lightblue;
}

td input {
  width: 50px;
}

.recipe-form, .recipe-container {
  width: 48%;
  margin: 0;
  float: left;
}

textarea {
  border: 1px solid lightgrey;
  border-radius: 5px;
}

.preview h1 {
  text-align: center;
  color: lightgrey;
}

